.table{
    margin-top: 20px;
    overflow: auto;
    height: 400px;
    /* color: #6s5d5d; */
}

.table tr:nth-of-type(odd){
    background-color: #f3f2f8;
}

.table tr{
    display: flex;
    justify-content: space-between;
}

.table tr td{
    text-align: left;
    padding: 0.5rem;
}